<template>
	<div id="order-dispatch">
		<public-header title="订单调度"></public-header>
		<div class="content">
			<div id="map-container"></div>
			<div class="icon icon-question1" @click="onAlert"></div>
			<div class="icon icon-refresh" @click="onLoad(true)"></div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {Amap} from '@/config/AMap'
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				id: 0,
				order: {},
				store: {},
				deliveryer: [],
				showPreLoading: true,
			}
		},
		methods: {
			onLoad(force) {
				var that = this;
				if(force) {
					that.deliveryer = [];
				}
				getData({
					vue: that,
					url: 'plateform/order/takeout/dispatch',
					data: {
						id: that.id,
					},
					success: (res) => {
						that.order = Object.assign(that.order, res.order);
						that.store = Object.assign(that.store, res.store);
						that.deliveryer = [...that.deliveryer, ...res.deliveryer];
						that.onLoadMap();
					}
				})
			},
			onLoadMap() {
				var that = this;
				Amap().then(function(AMap){
					var map = new AMap.Map('map-container', {
						resizeEnable: true,
						zoom: 12,
					});
					var marker = {};
					var markers = [];
					var deliveryer = that.deliveryer;
					var markerContent = '';
					for (var i = 0; i < deliveryer.length; i++) {
						if(deliveryer[i].location_x && deliveryer[i].location_y) {
							// 点标记显示内容，HTML要素字符串
							markerContent = '' +
								'<div class="deliveryer-item '+ deliveryer[i].css +'">' +
								'<div class="deliveryer-info flex-lr" id="deliveryer-info">'+
								'<span class="deliveryer-name">' + deliveryer[i]['title'] + '</span>'+
								'<span class="delivery-times-info">'+
								'<span class="wait-pickup">' + deliveryer[i]['wait_pickup'] + '</span>'+
								' / ' +
								'<span class="wait-delivery">' + deliveryer[i]['wait_delivery'] + '</span>'+
								'</span>'+
								'<span class="icon icon-attention hide" ></span>'+
								'</div>' +
								'<img class="location-dot" src="static/img/location_dot.png">' +
								'</div>';
							// 创建点实例
							marker = new AMap.Marker({
								position: new AMap.LngLat(deliveryer[i]['location_y'], deliveryer[i]['location_x']),
								content: markerContent,
								extData:{
									index: i
								}
							});
							//添加点击事件方式一
							 marker.on('click', function(e){
							 	var index = e.target.F.extData.index;
							 	var deliveryer_selected = that.deliveryer[index];
								var params = {
									url: 'plateform/order/takeout/dispatch',
									data: {
										dispatch: 1,
										id: that.id,
										deliveryer_id: deliveryer_selected.id,
									},
									confirm: '确定将订单指派给' + deliveryer_selected.title + '配送吗',
									success: function(message) {
										that.util.$toast(message);
										that.$router.replace(that.util.getUrl({path: '/pages/order/takeout',  query: {status: that.order.status}}));
									},
									fail: function(result) {
										if(result.errno == -1000) {
											params = {
												url: 'plateform/order/takeout/dispatch',
												data: {
													dispatch: 1,
													force: 1,
													id: that.id,
													deliveryer_id: deliveryer_selected.id,
												},
												confirm: '订单已经被配送员接单,确定重新指派吗？',
												success: function(message) {
													that.util.$toast(message);
													that.$router.replace(that.util.getUrl({path: '/pages/order/takeout',  query: {status: that.order.status}}));
												}
											};
											that.util.jspost(params);
										} else {
											that.util.$toast(result.message);
											return false;
										} 
									}
								};
								that.util.jspost(params);
							 });
							markers.push(marker);
						}
					}
					if(that.store.location_y && that.store.location_x) {
						marker = new AMap.Marker({
							position: [that.store.location_y, that.store.location_x],
							offset: new AMap.Pixel(-19, -52),
							content: '<div class="marker-start-route"></div>'
						});
						markers.push(marker);
					}
					if(that.order.location_y && that.order.location_x) {
						marker = new AMap.Marker({
							position: [that.order.location_y, that.order.location_x],
							offset: new AMap.Pixel(-19, -52),
							content: '<div class="marker-end-route"></div>'
						});
						markers.push(marker);
					}
					map.add(markers);
					if(that.order.location_y && that.order.location_x && that.store.location_y && that.store.location_x) {
						var lineArr = [
							[that.store.location_y, that.store.location_x],
							[that.order.location_y, that.order.location_x]
						];
						var polyline = new AMap.Polyline({
							path: lineArr,          //设置线覆盖物路径
							strokeColor: "#3366FF", //线颜色
							strokeOpacity: 1,       //线透明度
							strokeWeight: 3,        //线宽
							strokeStyle: "solid",   //线样式
							strokeDasharray: [10, 5] //补充线样式
						});
						polyline.setMap(map);
						map.setFitView();

						var driving = new AMap.Driving({
							map: map,
							panel: 'panel'
						});
						driving.search(new AMap.LngLat(that.store.location_y, that.store.location_x), new AMap.LngLat(that.order.location_y, that.order.location_x));
					}
					map.setFitView();
				});

			},
			onAlert() {
				this.$dialog.alert({
					message: '<p class="font-13">绿色标注代表配送员在线中且有未完成的外卖或跑腿订单; <br> 灰色标注代表配送员离线中(5分钟没有上传地址位置即视为离线); <br> 红色标注代表配送员在线中且没有未完成的跑腿和外卖订单;<br>外卖订单数据说明：凌晨5:00之前配送员手中订单包括前一天的订单数据，凌晨5:00之后则是当天的数据</p>'
				})
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id) {
				this.id = this.$route.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#order-dispatch  .icon-refresh{
		position: absolute;
		top: 10px;
		right: 10px;
		width: 40px;
		height: 40px;
		line-height: 40px;
		border-radius: 50%;
		background-color: rgba(0,0,0,0.3);
		color: #fff;
		text-align: center;
		font-size: 18px;
	}
	#order-dispatch .icon-question1{
		position: absolute;
		bottom: 60px;
		right: 15px;
		width: 40px;
		height: 40px;
		line-height: 40px;
		text-align: center;
		border-radius: 50%;
		background-color: rgba(0,0,0,0.3);
		font-size: 18px;
		color: #fff;
	}
	#order-dispatch #map-container{
		width: 100%;
		height: 100%;
	}
	#order-dispatch .deliveryer-item{
		border-radius: 2px;
		position: relative;
		z-index: 19;
		font-size: 14px;
		text-align: center;
		line-height: 30px;
		border: 1px solid #1ab394;
		background: #f4fffc;
		color: #1ab394;
	}
	#order-dispatch .deliveryer-item.off-line{
		border: 1px solid #999;
		background: #eee;
		color: #999;
	}
	#order-dispatch .deliveryer-item.active{
		border-color: #ff2d4b;
		background: #fff3f4;
		color: #ff2d4b;
	}
	#order-dispatch .deliveryer-item:before, #order-dispatch .deliveryer-item:after{
		content: "";
		display: block;
		border-width: 10px;
		position: absolute;
		bottom: -20px;
		left: 30px;
		border-style: solid dashed dashed;
		border-color: #1ab394 transparent transparent;
		font-size: 0;
		line-height: 0;
		z-index: 10;
	}
	#order-dispatch .deliveryer-item:before{
		bottom: -20px;
		border-color: #1ab394 transparent transparent;
	}
	#order-dispatch .deliveryer-item:after{
		bottom: -19px;
		border-color: #f4fffc transparent transparent;
	}
	#order-dispatch .deliveryer-item.off-line:before{
		bottom: -20px;
		border-color: #999 transparent transparent;
	}
	#order-dispatch .deliveryer-item.off-line:after{
		bottom: -19px;
		border-color: #eee transparent transparent;
	}
	#order-dispatch .deliveryer-item.active:before{
		bottom: -20px;
		border-color: #ff2d4b transparent transparent;
	}
	#order-dispatch .deliveryer-item.active:after{
		bottom: -19px;
		border-color: #fff3f4 transparent transparent;
	}
	#order-dispatch .deliveryer-item .deliveryer-name{
		display: block;
		font-size: 14px;
		width: 60px;
		height: 30px;
	}
	#order-dispatch .deliveryer-item .delivery-times-info{
		width: 40px;
		background-color: #fff;
		height: 30px;
		color: #333;
	}
	#order-dispatch .deliveryer-item .delivery-times-info .wait-delivery{
		color: #f90;
	}
	#order-dispatch .deliveryer-item .icon{
		width: 30px;
		height: 30px;
		font-size: 16px;
	}
	#order-dispatch .deliveryer-item .location-dot{
		position: absolute;
		left: 30px;
		bottom: -18px;
		width: 21px;
		height: 15px;
		z-index: 9;
	}
	#order-dispatch  .marker-end-route{
		position: relative;
		width: 38px;
		height: 52px;
		color: #e90000;
		background: url(../../../static/img/map-end.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
	#order-dispatch  .marker-start-route{
		position: relative;
		width: 38px;
		height: 52px;
		color: #e90000;
		background: url(../../../static/img/map-start.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
</style>


